@import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700");
.leftColumn {
  background: #333; }

.name {
  color: #fff;
  font-family: "Montserrat";
  font-weight: bold;
  font-size: 37px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 6px; }

.profession {
  font-size: 12px;
  font-family: "Montserrat";
  color: #fff;
  letter-spacing: 5px;
  text-align: center;
  text-transform: uppercase; }

.workExperience {
  color: #232021;
  font-family: "Montserrat";
  font-size: 16px; }

.lineLeft {
  height: 1px;
  width: 100%;
  background: #fff;
  margin: 50px 0; }

@media (min-width: 1200px) {
  .avatarImg {
    padding-top: 90px;
    margin: 0 auto;
    display: block;
    width: 30%; }
  .avatar {
    background: url("../../assets/bg-avatar.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80%;
    height: 300px; } }

@media (min-width: 991px) and (max-width: 1199px) {
  .avatarImg {
    padding-top: 90px;
    margin: 0 auto;
    display: block;
    width: 40%; }
  .avatar {
    background: url("../../assets/bg-avatar.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    height: 300px; } }

@media (min-width: 768px) and (max-width: 990px) {
  .avatarImg {
    padding-top: 105px;
    margin: 0 auto;
    display: block;
    width: 38%; }
  .avatar {
    background: url("../../assets/bg-avatar.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 95%;
    height: 300px; } }

@media (max-width: 767px) {
  .avatarImg {
    padding-top: 100px;
    margin: 0 auto;
    display: block;
    width: 22%; }
  .avatar {
    background: url("../../assets/bg-avatar.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
    height: 300px; } }

@media (max-width: 338px) {
  .avatarImg {
    padding-top: 110px;
    margin: 0 auto;
    display: block;
    width: 30%; }
  .avatar {
    background: url("../../assets/bg-avatar.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80%;
    height: 300px; } }

.profile, .contact, .skills {
  text-transform: uppercase;
  text-align: center;
  color: #FBFCFB;
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 16px; }

.skills {
  margin-top: 30px; }

.contact {
  padding-top: 30px; }

.hrLeft {
  height: 1px;
  width: 50%;
  background: #D2D0CF; }

.descript {
  font-size: 12px;
  font-family: "Montserrat";
  text-align: center;
  color: #D2D0CF;
  width: 80%;
  margin: 0 auto;
  letter-spacing: 2px; }

.tel, .cel {
  color: #D2D0CF;
  text-align: center;
  letter-spacing: 2px;
  font-size: 14px;
  font-family: "Montserrat"; }

.address, .mail, .website {
  font-size: 12px;
  font-family: "Montserrat";
  text-align: center;
  color: #D2D0CF;
  width: 80%;
  margin: 0 auto;
  letter-spacing: 3px; }

.address {
  margin-bottom: 30px;
  margin-top: 20px; }

.mail {
  margin-bottom: 30px; }

.progressBar {
  background-color: #333 !important;
  border-radius: 0 !important;
  border: 1px solid #fff;
  height: 20px !important;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 25px; }

.titleSkill {
  font-family: "Montserrat" !important;
  font-size: 12px;
  font-weight: 100;
  text-align: center;
  text-transform: uppercase;
  color: #FBFCFB;
  letter-spacing: 2px;
  padding-top: 0px; }

.workExperience, .education {
  text-transform: uppercase;
  font-weight: bold;
  font-family: "Montserrat";
  font-size: 20px;
  letter-spacing: 2px; }

.workExperience {
  padding-top: 50px; }

.hrRight {
  border: 2px solid #333;
  margin: -10px 0 20px 0; }

.jobTitle, .eduTitle {
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px; }

.jobDate, .eduDate {
  font-family: "Montserrat";
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 2px; }

.jobDescript, .eduDescript {
  font-family: "Montserrat";
  font-size: 13px;
  font-weight: 600;
  margin-bottom: 40px;
  font-weight: 600; }

.avatarImg {
  border-bottom-right-radius: 30%;
  border-bottom-left-radius: 30%; }

@media (min-width: 500px) {
  .rightColumn {
    margin-left: 20px;
    border-right: solid 1px grey;
    border-bottom: solid 1px grey;
    border-top: solid 1px grey; } }



